header .mdl-layout-title {
  display: none;
}

header.mdl-layout__header {
  background-color: #252525;
}

.mdl-navigation__link {
  font-size: 1.1rem;
  letter-spacing: 0.4px;
  font-weight: 700;
}

.mdl-layout__drawer .mdl-navigation .mdl-navigation__link {
  color: #424242;
}
.Icon {
  font-size: 2.3rem;
}

.Icon__link {
  color: #303f9f;
}
.Banner {
  display: flex;
  position: relative;
  flex-direction: column;
  min-height: 35rem;
  height: calc(100vh - 50px);
  max-height: 70rem;
}

/* all banner sections */

.Banner > div {
  flex: 1 1;
  z-index: 999;
  padding: 0;
  width: 100%;
  max-width: 80rem;
}

/* Banner Hero */

.Banner__hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 1rem;
  align-items: center;
}

.Banner__hero > div {
  text-align: center;
}

.Banner__hero > .titles {
  text-shadow: 1px 1px 3px #8e8e8e94;
}

.Banner__title {
  margin: 0;
  font-size: 3.8rem;
  font-weight: 300;
}

.Banner__subtitle {
  font-weight: 400;
  font-size: 2.2rem;
  margin: 0;
}

.Banner__hero > .social-media {
  margin-top: 1rem;
}

.Banner__hero > .social-media .Icon {
  margin: 0 1rem;
}

/* overlays */

.Banner > .color__overlay {
  position: absolute;
  background: #efefef73;
  width: 100%;
  z-index: 0;
  height: 100%;
}

.Banner > .image__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
}

.Banner > .image__overlay img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}



